import React from 'react';
import './inputItem.scss';

function InputItem(props) {
  const {
    customStyle,
    onChange,
    disabled,
    onClick,
    customInputStyle,
    value,
  } = props;
  return (
    <div onClick={onClick} className={`input-item-container ${customStyle}`}>
      {props.icon}
      <input
        value={value}
        readOnly={disabled}
        className={`input ${customInputStyle}`}
        placeholder={props.text}
        onChange={(e) => {
          onChange(e.target.value);
        }}
      />
    </div>
  );
}

export default InputItem;
